import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from "rxjs/Subscription";
import {ActivatedRoute, Router} from "@angular/router";
import {OriginationService} from "../../origination.service";
import {NgForm} from "@angular/forms";

@Component({
  selector: 'app-list-user',
  templateUrl: './list-user.component.html',
  styleUrls: ['./list-user.component.css']
})
export class ListUserComponent  implements OnInit, OnDestroy {

  users: Array<any>;

  sub: Subscription;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private originationService: OriginationService) {
  }

  ngOnInit() {
    /*this.originationService.getUser("5bbcba8efa9c47ba2ddb124d").subscribe(data => {
      this.user = data;
    });*/
    this.sub = this.route.params.subscribe(params => {
      this.originationService.getAll().subscribe((users: any) => {
        if (users) {
          this.users = users;
        } else {
          console.log(`No list found`);
          this.ngOnInit();
        }
      });
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  addIdentification(id:string){
    console.log(id)
    this.router.navigate(["/identification",id]);
  }

  addIncome(id:string){
    console.log(id)
    this.router.navigate(["/income",id]);
  }

  addBankAccount(id:string){
    console.log(id)
    this.router.navigate(["/bankAccount",id]);
  }

  addPaymentMethod(id:string){
    console.log(id)
    this.router.navigate(["/paymentMethod",id]);
  }

  showUser(id:string){
    console.log(id)
    this.router.navigate(["/getUser",id]);
  }

  saveUser(id:string){
    console.log(id)
    this.router.navigate(["/saveUser",id]);
  }

  reviewStandardInfo(id:string){
    console.log(id);
    this.router.navigate(["/reviewStandardInfo",id]);
  }

  showLoans(id:string){
    console.log(id);
    this.router.navigate(["/showLoans",id]);
  }

  remove(id) {
    this.originationService.removeUser(id).subscribe(result => {
      console.log(result)
      this.ngOnInit()
    }, error => console.error(error));
  }

}
